import { AdController, ShowPromiseResult } from "../sdk/adsgram";


export class H5AdSdkTelegram {
    private static _instance: H5AdSdkTelegram;
    private constructor() { }
    public static get instance(): H5AdSdkTelegram {
        if (!this._instance) {
            this._instance = new H5AdSdkTelegram();
            this._instance.tgInit();
        }
        return this._instance;
    }

    private blockId: string = "5585";
    private ad: AdController

    private tgInit(){
        if (this.blockId) {
            this.ad = window.Adsgram.init({blockId: this.blockId})
        }
    }

    /**
     * 
     * tmpFunc(1)
     * tmpFunc(0)
     * @param cb 
     */
    showRewardedAd(successCb: Function) {
        if (this.ad) {
            this.ad.show().then((result: ShowPromiseResult) =>{
                successCb(1);
            }).catch((result: ShowPromiseResult) => {
                console.log('Tg', result.description)
                successCb(0)
            });
            return
        }
        successCb(2)
    }


}